<template>
  <div id="qusetions">
    <my-title :a_red="true" :a_size="'1rem'" class="my_title" :img="require('@/common/images/上下@2x.png')"
              :title="'常见问题'" :more_title="'点击量'"
              @clickP="sortType"></my-title>
    <my-problem-p v-for="(item ,index) in problems" :data="item" :index="index" :key=index></my-problem-p>
  </div>
</template>

<script>
import myTitle from '../../../components/myTitle'
import myProblemP from '../../../components/myProblemP'

import myUtil from '../../../common/myUtil'

export default {
  name: 'qusetions',
  components: {
    myTitle,
    myProblemP
  },
  data () {
    return {
      currentSort: 0,
      problemsStore: [],
      problems: [
        {
          title: '创业1板转签如何办理',
          id: 23156,
          clickN: 1,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业2板转签如何办理',
          id: 23156,
          clickN: 2,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转3签如何办理',
          id: 23156,
          clickN: 3,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转4签如何办理',
          id: 23156,
          clickN: 4,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签如何5办理',
          id: 23156,
          clickN: 5,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签如何8办理',
          id: 23156,
          clickN: 8,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签如何9办理',
          id: 23156,
          clickN: 9,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签6如何办理',
          id: 23156,
          clickN: 6,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签如7何办理',
          id: 23156,
          clickN: 7,
          time: '2019/11/15 10:36:55'
        }, {
          title: '创业板转签如何办10理',
          id: 23156,
          clickN: 10,
          time: '2019/11/15 10:36:55'
        }
      ]
    }
  },
  mounted () {
    this.init()
  },
  methods: {
    init () {
      this.query()
      this.problemsStore = this.problems
    },
    to () {
    },
    query () {
      this.findByPageQ()
    },
    findByPageQ () {
      this.$http.findByPageQ({
        sortType: 0,
        sort: 0,
        page: 0,
        size: 5
      }).then((res) => {
      })
    },
    sortType () {
      if (this.currentSort === 0) {
        this.problems = myUtil.sortObject(this.problems, 'clickN')
        this.currentSort = 1
      } else {
        this.problems = this.problemsStore
        this.currentSort = 0
      }
    }
  }
}
</script>

<style scoped>
  #qusetions {
    position: fixed;
    top: 3rem;
    left: 0;
    width: 100%;
    color: #F5F6F7;
    bottom: 0;
    overflow-y: auto;
  }

  #qusetions .my_title a .active {
    color: red;
  }
</style>



